import React, { useState, useEffect, useRef } from "react";
import "./TechnologyCard.css";
import Modal from "../Modal/Modal";
import { createPortal } from "react-dom";

const statusLabels = {
  completed: "",
  "in-progress": " ",
  "not-started": " ",
};

export default function TechnologyCard({
  id,
  title,
  description,
  status,
  onToggle,
  note = "",
  onNoteChange = () => {},
  links = [],
  isUserLoggedIn = false,
}) {
  const label = statusLabels[status] || "";
  const [isModalOpen, setModalOpen] = useState(false);
  const [isVisible, setIsVisible] = useState(false);
  const cardRef = useRef(null);

  useEffect(() => {
    const observer = new IntersectionObserver(
      ([entry]) => {
        if (entry.isIntersecting) {
          setIsVisible(true);
          observer.unobserve(entry.target);
        }
      },
      { threshold: 0.1 }
    );
    if (cardRef.current) observer.observe(cardRef.current);
    return () => observer.disconnect();
  }, []);

  const handleToggleModal = (e) => {
    e.stopPropagation();
    setModalOpen((prev) => {
      const newState = !prev;
      document.body.classList.toggle("no-scroll", newState);
      return newState;
    });
  };

  const handleCloseModal = () => {
    setModalOpen(false);
    document.body.classList.remove("no-scroll");
  };

  return (
    <article
      ref={cardRef}
      className={`technology-card ${status} status-animation ${
        isVisible ? "visible" : ""
      } ${!isUserLoggedIn ? "disabled" : ""}`}
      onClick={() => isUserLoggedIn && onToggle?.(id)}
      title={!isUserLoggedIn ? ",   " : ""}
    >
      <h4 data-status={status}>{title}</h4>
      <small>: {label}</small>
      <div className="field" onClick={(e) => e.stopPropagation()}>
        <label> :</label>
        <textarea
          value={note}
          onChange={(e) => isUserLoggedIn && onNoteChange(e.target.value)}
          placeholder={
            isUserLoggedIn
              ? "   ..."
              : ",   "
          }
          disabled={!isUserLoggedIn}
        />
      </div>
      <div className="card-bottom">
        <p className="muted">{note ? " " : "\u00A0"}</p>
        <button className="more-btn" onClick={handleToggleModal}>
          
        </button>
      </div>
      {isModalOpen &&
        createPortal(
          <Modal
            title={title}
            content={description}
            links={links}
            onOutsideClick={handleCloseModal}
            onClose={handleCloseModal}
          />,
          document.body
        )}
    </article>
  );
}
