import { useState, useEffect } from "react";
import "./DataImportExport.css";

function DataImportExport() {
  const [technologies, setTechnologies] = useState([]);
  const [status, setStatus] = useState("");

  useEffect(() => {
    const savedData = localStorage.getItem("technologies");
    if (savedData) {
      try {
        const parsedData = JSON.parse(savedData);
        setTechnologies(parsedData);
        setStatus(` ${parsedData.length}   `);
      } catch (error) {
        setStatus("    ");
      }
    }
  }, []);

  useEffect(() => {
    if (technologies.length > 0) {
      localStorage.setItem("technologies", JSON.stringify(technologies));
    }
  }, [technologies]);

  const handleExport = () => {
    const exportData = {
      version: "1.0",
      exportedAt: new Date().toISOString(),
      technologies: technologies,
      stats: {
        total: technologies.length,
        completed: technologies.filter((t) => t.status === "completed").length,
        inProgress: technologies.filter((t) => t.status === "in-progress")
          .length,
      },
    };

    const dataStr = JSON.stringify(exportData, null, 2);
    const blob = new Blob([dataStr], { type: "application/json" });
    const url = URL.createObjectURL(blob);

    const link = document.createElement("a");
    link.href = url;
    link.download = `tech-tracker-${
      new Date().toISOString().split("T")[0]
    }.json`;
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
    URL.revokeObjectURL(url);

    setStatus(` ${technologies.length} `);
  };

  const handleImport = (event) => {
    const file = event.target.files[0];
    if (!file) return;

    const reader = new FileReader();

    reader.onload = (e) => {
      try {
        const fileContent = e.target.result;
        const importedData = JSON.parse(fileContent);

        let validTechnologies = [];

        if (
          importedData.technologies &&
          Array.isArray(importedData.technologies)
        ) {
          validTechnologies = importedData.technologies.filter((tech) => {
            return tech.title && tech.description;
          });
        } else if (
          typeof importedData === "object" &&
          !Array.isArray(importedData)
        ) {
          validTechnologies = Object.entries(importedData)
            .map(([id, tech]) => {
              if (tech && tech.title && tech.description) {
                return {
                  id: id,
                  title: tech.title,
                  description: tech.description,
                  status: tech.status || "not-started",
                  category: tech.category || "general",
                  links: tech.links || [],
                  createdAt: tech.createdAt || new Date().toISOString(),
                };
              }
              return null;
            })
            .filter((tech) => tech !== null);
        }

        if (validTechnologies.length === 0) {
          throw new Error(
            "     .   ."
          );
        }

        if (technologies.length > 0) {
          const confirmReplace = window.confirm(
            ` ${validTechnologies.length} .\n\n` +
              ` -    (${technologies.length} .)\n` +
              ` -    `
          );

          if (confirmReplace) {
            setTechnologies(validTechnologies);
            setStatus(
              `!  ${validTechnologies.length} `
            );
          } else {
            const maxId = Math.max(
              ...technologies.map((t) => (typeof t.id === "number" ? t.id : 0)),
              0
            );
            const techsWithNewIds = validTechnologies.map((tech, index) => ({
              ...tech,
              id: maxId + index + 1,
            }));
            setTechnologies((prev) => [...prev, ...techsWithNewIds]);
            setStatus(
              `!  ${validTechnologies.length}    ${technologies.length}`
            );
          }
        } else {
          setTechnologies(validTechnologies);
          setStatus(` ${validTechnologies.length} `);
        }
      } catch (error) {
        setStatus(` : ${error.message}`);
      }
    };

    reader.onerror = () => {
      setStatus("  ");
    };

    reader.readAsText(file);

    event.target.value = "";
  };

  const addSampleTechnology = () => {
    const newTech = {
      id: Date.now(),
      title: ` ${technologies.length + 1}`,
      description: "   ",
      status: "not-started",
      category: "frontend",
      createdAt: new Date().toISOString(),
    };

    setTechnologies((prev) => [...prev, newTech]);
    setStatus("  ");
  };

  const clearAllData = () => {
    const confirmClear = window.confirm(
      " ,     ?"
    );
    if (confirmClear) {
      setTechnologies([]);
      localStorage.removeItem("technologies");
      setStatus("  ");
    }
  };

  const toggleStatus = (techId) => {
    setTechnologies((prev) =>
      prev.map((tech) => {
        if (tech.id === techId) {
          const statuses = ["not-started", "in-progress", "completed"];
          const currentIndex = statuses.indexOf(tech.status || "not-started");
          const nextStatus = statuses[(currentIndex + 1) % statuses.length];
          return { ...tech, status: nextStatus };
        }
        return tech;
      })
    );
  };

  const getStatusText = (status) => {
    switch (status) {
      case "completed":
        return " ";
      case "in-progress":
        return "  ";
      default:
        return "  ";
    }
  };

  return (
    <div className="data-import-export">
      <h1>/ </h1>

      {status && <div className="status-message">{status}</div>}

      <div className="control-panel">
        <button
          onClick={handleExport}
          disabled={technologies.length === 0}
          className="btn btn-export"
        >
            
        </button>

        <label className="btn btn-import">
            
          <input
            type="file"
            accept=".json"
            onChange={handleImport}
            style={{ display: "none" }}
          />
        </label>

        <button onClick={addSampleTechnology} className="btn btn-add">
             
        </button>

        <button
          onClick={clearAllData}
          disabled={technologies.length === 0}
          className="btn btn-clear"
        >
             
        </button>
      </div>

      <div className="technologies-section">
        <h2> ({technologies.length})</h2>

        {technologies.length === 0 ? (
          <div className="empty-state">
            <p>    </p>
            <p>     </p>
          </div>
        ) : (
          <div className="technologies-grid">
            {technologies.map((tech) => (
              <div key={tech.id} className="tech-card">
                <h3>{tech.title}</h3>
                <p>{tech.description}</p>
                <div className="tech-meta">
                  <span className="category">
                    {tech.category || " "}
                  </span>
                  <button
                    onClick={() => toggleStatus(tech.id)}
                    className={`status-badge status-${
                      tech.status || "not-started"
                    }`}
                  >
                    {getStatusText(tech.status)}
                  </button>
                </div>
              </div>
            ))}
          </div>
        )}
      </div>

      <div className="instructions">
        <h3> </h3>
        <ul>
          <li>
            <strong>:</strong>     JSON 
          </li>
          <li>
            <strong>:</strong>    JSON 
          </li>
          <li>
            <strong>:</strong>    
            
          </li>
          <li>
            <strong>:</strong>     
          </li>
        </ul>
      </div>
    </div>
  );
}

export default DataImportExport;
